body {
	margin: 0;
	padding: 0;
	font-family: sans-serif;
	background: #DCE35B;
	/* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #45B649, #DCE35B);
	/* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(linear, left top, right top, from(#45B649), to(#DCE35B));
	background: -webkit-linear-gradient(left, #45B649, #DCE35B);
	background: -o-linear-gradient(left, #45B649, #DCE35B);
	background: linear-gradient(to right, #45B649, #DCE35B);
	/* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	min-height: 100vh;
}


